#menuNav {
  &.fixed {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1001;
    -webkit-transition: top .3s;
    -moz-transition: top .3s;
    -ms-transition: top .3s;
    -o-transition: top .3s;
    transition: top .3s;
  }
  .nav-justified {
    ul {
      margin: 0;
      border: 0;
      border-radius: 0;
    }
    a {
      font-weight: 500;
    }
    .dropdown-menu > li > a {
      color: #fff;
      font-weight: 400;

    }
    .open > a {
      background-color: transparent;
    }
    .dropdown {
      ul {
        top: 100%;
        background-color: #00904a;
        min-width: 100%;
        padding: 10px 0;
        li {
          padding: 5px 0;
          &:hover {
            background-color: #0b5f2e;
            a {
              color: #fff;
            }
          }
        }
      }
      .dropdown-menu > li > a:hover, .dropdown-menu > li > a:focus {
        background-color: transparent;
        color: #fff;
      }
    }
    li.dropdown.open {
      background-color: #00904a;
      a {
        color: #fff;
      }
      ul li a {
        color: #d8ede5;
        font-size: 16px;
      }
    }
    > li {
      position: relative;
      vertical-align: middle;
      text-align: left;
      border-bottom: none;
      padding-right: 15px;
      width: auto;
      .arrow-down {
        img {
          width: 11px;
        }
      }
      &.dropdown:hover, &.dropdown.active.open {
        .arrow-down {
          background: url('/bundles/imsitemaster/img/icons/menue-desktop-arrow-hover.svg') no-repeat;
          background-position-y: 7px;
          background-size: 12px;
          img {
            opacity: 0;
          }
        }
      }
      &.active {
        > a {
          border-bottom: 3px solid #00904a;
          color: #00904a;
        }
      }
      &:hover {
        background-color: #00904a;
        a {
          color: #fff;
        }
      }
      > a {
        color: #8c8c8c;
        display: inline-block;
        padding: 15px 0;
        border-bottom: 3px solid transparent;
        margin-left: 15px;
        font-size: 16px;
        @media (min-width: 992px) {
          white-space: nowrap;
        }
        &:hover {
          background-color: transparent;
        }
        &:focus {
          background-color: transparent;
        }
      }
    }
  }
  &.navbar {
    background-color: #fff;
    @media (max-width: @mobile-min-width) {
      box-shadow: 0 0 1px 1px #dad8d8;
    }
  }
  .navbar-header {
    @media (max-width: @mobile-min-width) {
      height: 60px;
      float: none;
    }
    button {
      margin-left: 20px;
      margin-top: 20px;
    }
    a {
      width: 100%;
      img {
        margin: 0 auto;
      }
    }
    .header_cart_mobile {
      position: absolute;
      right: 20px;
      top: 20px;
    }
  }
  .mobile_header_text {
    text-align: center;
  }
}

#menuNav {
  height: auto !important;
  border-radius: 0;
  margin-bottom: 0;
  @media (max-width: @mobile-min-width) {
    border: none;
  }
}

@media (max-width: @mobile-min-width) and (min-width: @mobile-max-width) {
  #menuNav {
    min-height: 100px;
  }
}

#mobile-menu {
  margin-bottom: 0;
  border: 0;
  border-radius: 0;
  height: 100%;
  position: fixed;
  left: 0px;
  top: 0px;
  -webkit-transition: width 0.3s ease-out 0s;
  -moz-transition: width 0.3s ease-out 0s;
  -ms-transition: width 0.3s ease-out 0s;
  -o-transition: width 0.3s ease-out 0s;
  width: 0;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 100000;
  background: #00904a repeat-x center;
  background-size: auto 100%;
  max-width: 285px;
  .container-fluid {
    min-width: 285px;
    padding-left: 15px;
    padding-right: 15px;
    @media (min-width: 768px) {
      padding-left: 0;
      padding-right: 0;
    }
    .navbar-collapse {
      width: auto;
      @media (max-width: @mobile-min-width) {
        margin-right: -15px;
        margin-left: -15px;
      }
    }
  }
  .navbar-nav {
    margin-top: 0;
    margin-bottom: 0;
  }
  ul {
    float: none;
    list-style-type: none;
    padding: 0;
    .dropdown-menu {
      li:hover, li:focus {
        background-color: #0b5f2e;
        a {
          color: #fff;
        }
      }
      li a {
        color: #bee3d2;
        font-size: 16px;
        white-space: normal;
      }
    }
  }
  ul.navbar-nav > li {
    border-bottom: 1px solid #1b995a;
    &.active > a {
      color: #fff;
    }
    > a {
      font-size: 16px;
      line-height: 30px;
      color: #bee3d2;
      text-shadow: 0 2px 1px rgba(11, 59, 88, 0.3);
      padding: 15px 10px;
      text-decoration: none;
      cursor: pointer;
      &:hover, &:focus {
        background-color: #0b5f2e;
        color: #fff;
      }
    }
    &.open > a {
      background-color: #0b5f2e;
    }
  }
  ul li {
    float: none;
    display: block;
    &.open {
      img {
        -webkit-transform: rotate(180deg);
        transform: rotate(180deg);
        transition: -webkit-transform 0.1s;
        transition: transform 0.1s;
        transition: transform 0.1s, -webkit-transform 0.1s;
      }
    }
    .arrow-down {
      img {
        position: absolute;
        right: 15px;
        top: 25px;
        transition: -webkit-transform 0.1s;
        transition: transform 0.1s;
        transition: transform 0.1s, -webkit-transform 0.1s;
      }
    }
    .dropdown-menu {
      li {
        padding: 10px 0;
      }
    }
  }
  .navbar-nav > .active > a {
    background-color: #0b5f2e;
  }
  &.activated {
    width: 285px;
    @media (min-width: 992px) {
      width: 0;
    }
  }
}

